<template>
  <div>
    <div class="user" id="join" style=" text-align:center;">
      <div class="wrapC table">
        <div class="middle" style="text-align:left;">
          <h1>정보수정</h1>
          <div
            class="wrap-input100 validate-input"
            data-validate="Valid email is required: kimssafy@ssafy.com"
          >
            <span class="partition-title">Email</span>
            <span class="focus-input100" style="margin-left:20px">{{user.email}}</span>
          </div>
          <hr />
          <div class="wrap-input100 validate-input" data-validate="Username is required">
            <span class="partition-title">ID</span>
            <span class="focus-input100" style="margin-left:20px">{{user.uid}}</span>
          </div>
          <hr />
          <div class="wrap-input100 validate-input" data-validate="Password is required">
            <span class="label-input100 mx-1">PW</span>

            <input
              class="input100 mx-1"
              type="password"
              id="userpwd"
              name="userpwd"
              value="${userinfo}"
              placeholder
              v-model="password"
              style="margin-left:20px"
            />
            <span class="focus-input100"></span>
          </div>
          <hr />
          <div class="wrap-input100 validate-input" data-validate="Repeat Password is required">
            <span class="label-input100 mx-1">RE</span>

            <input
              class="input100 mx-1"
              type="password"
              id="pwdcheck"
              name="pwdcheck"
              placeholder="*************"
              v-model="passwordcon"
              style="margin-left:20px"
            />
            <span class="focus-input100"></span>
          </div>
          <hr />

          <div class="fieldlabel">
            <label for="profile">프로필사진</label>
          </div>
          <div class="formfield">
            <input type="file" id="profile" name="profile" required />
          </div>
          <hr />
          <div class="wrap-input100 validate-input" data-validate="Username is required">
            <span class="label-input100 mx-1">자기소개</span>
            <input
              class="input100"
              id="introduce"
              name="introduce"
              placeholder
              v-model="introduce"
              style="margin-left:20px"
            />
            <span class="focus-input100"></span>
          </div>

          <div style="text-align:left;">
            <br />
            <button @click="signup" class="raise" style="margin:10px; margin-left:100px; text-align:center;">수정</button>
          </div>
        </div>
      </div>
    </div>
    <v-footer app color="#B677FF" height="60px">
      <span class="footer_s">&copy; {{ new Date().getFullYear() }} 이 블로그의 저작권은 싸피탈출넘버원에있습니다.</span>
    </v-footer>
  </div>
</template>

<script>
import axios from "@/util/axios";

export default {
  name: "SignupRequest",
  data() {
    return {
      user: {},
      password: "",
      introduce: "",
      passwordcon: "",
    };
  },
  methods: {
    signup: function () {
      console.log(this.password);
      if (this.password !== this.passwordcon) {
        alert("비밀번호가 다릅니다.");
      } else {
        axios({
          method: "PUT",
          url: "/user",
          data: {
            uid: this.user.uid,
            password: this.password,
            email: this.user.email,
            introduce: this.introduce,
          },
        })
          .then((Response) => {
            alert("수정이 완료되었습니다.");
            this.$router.go(-1);
          })
          .catch((ex) => {
            var errorcode = ex.response.status;
            this.$router.push("/errorpage/" + errorcode);
          });
      }
    },
  },
  mounted() {
    var uid = this.$session.get("uid");
    axios
      .post("/user/update", uid)
      .then((Response) => {
        this.user = Response.data;
      })
      .catch((ex) => {
        var errorcode = ex.response.status;
        this.$router.push("/errorpage/" + errorcode);
      });
  },
};
</script>
