<template>
  <v-app id="inspire">
    <modal name="my-first-modal2">
      <h1>본 싸이트에 저장된 정보는 전부 싸피탈출넘버원에서 관리합니다^^

        우리팀 화이팅!!!!!!!!!!
      </h1>
      <div class="box"></div>
    </modal>

    <div class="user" id="join" >
      <div class="wrapC table">
        <div class="middle">
          <h1>회원가입</h1>
          <div class="form-wrap">
            <div class="input-wrap">
              <input v-model="signupData.uid" id="nickname" placeholder="아이디를 입력해주세요" type="text" />
            </div>

            <div class="input-wrap">
              <input v-model="signupData.email" id="email" placeholder="이메일을 입력해주세요" type="text" />
            </div>

            <div class="input-wrap password-wrap">
              <input
                v-model="signupData.password"
                id="password"
                type="password"
                placeholder="비밀번호를 입력해주세요"
              />

            </div>

            <div class="input-wrap password-wrap">
              <input
                v-model="passwordConfirm"
                id="password-confirm"
                type="password"
                placeholder="비밀번호를 한번 더 입력해주세요"
              />
   
              </span>
            </div>
          </div>

          <label>
            <input v-model="isTerm" type="checkbox" id="term" />
            <span>약관에 동의합니다</span>
          </label>

          <button class="mx-5" @click="show">약관보기</button>

          <button @click="signup" class="btn">
            <span>작성완료</span>
          </button>

        </div>
      </div>
    </div>
    <v-footer app color="#B677FF" height="60px">
      <span class="footer_s">&copy; {{ new Date().getFullYear() }} 이 블로그의 저작권은 싸피탈출넘버원에있습니다.</span>
    </v-footer>
  </v-app>
</template>

<script>
import "../../assets/css/user.scss";
import axios from "@/util/axios";
export default {
  components: {},
  created() {},
  data: () => {
    return {
      signupData: {
        uid: "",
        email: "",
        password: "",
      },
      passwordConfirm: "",
      isTerm: false,
    };
  },
  methods: {
    show() {
      this.$modal.show("my-first-modal2");
    },
    hide() {
      this.$modal.hide("my-first-modal2");
    },
    signup() {
      if (this.isTerm === false) {
        alert("약관 동의에 체크해주세요");
      } else if (this.signupData.password !== this.passwordConfirm) {
        alert("비밀번호를 확인해주세요");
      } else {
          console.log(this.signupData)
          axios.post('/user', this.signupData)
          .then(res => {
            alert("회원가입이 완료되었습니다.");
            this.$router.push('/')
          })
          .catch(err => console.log('에러다짜식아'))
      }
    },
  },
  mounted() {
    this.$modal.hide("my-first-modal");
  },
};
</script>
