<template>
<div>
  <!-- 로그인 모달 -->
    <modal name="my-first-modal">
      <div class="box">
        <div class="box-part">
          <div id="partition-register" class="partition">
            <div class="partition-title">LOGIN</div>
            <div class="partition-form">
              <form autocomplete="false">
                <div class="autocomplete-fix">
                  <input type="password" />
                </div>
                <input id="n-email" type="text" placeholder="Email" v-model="loginData.email" />
                <input
                  id="n-password2"
                  type="password"
                  placeholder="Password"
                  v-model="loginData.password"
                />
              </form>
              <div style="margin-top: 42px;"></div>
              <div class="button-set">
                <button id="goto-signin-btn" @click="signin">로그인</button>
                <button id="goto-signin-btn">
                  <router-link v-bind:to="{name:constants.URL_TYPE.USER.JOIN}">회원가입</router-link>
                </button>
              </div>
            </div>
            <div id="bp-right" class="box-part">
              <div class="box-messages"></div>
            </div>
          </div>
        </div>
      </div>
    </modal>

    <!-- 리스트 -->
    <div v-if="this.$session.get('uid') != null">
      
          <v-navigation-drawer
        v-model="drawer"
        temporary
        app right clipped color="#ecbef8"
      >
        <v-list dense>
          <!-- 회원수정 -->
          <v-list-item link>
            <v-list-item-action>
              <v-icon>mdi-view-dashboard</v-icon>
            </v-list-item-action>
            <div @click="userupdate">
              <v-list-item-content>
                <span>회원수정</span>
              </v-list-item-content>
            </div>
          </v-list-item>
<!-- 포스팅하기 -->
          <v-list-item link>
            <v-list-item-action>
              <v-icon>mdi-view-dashboard</v-icon>
            </v-list-item-action>
            <v-list-item-content>
              <div @click="createblog">
                <span>포스팅하기</span>
              </div>
            </v-list-item-content>
          </v-list-item>

          <!-- 내가 쓴 글 -->
          <v-list-item link>
            <v-list-item-action>
              <v-icon>mdi-view-dashboard</v-icon>
            </v-list-item-action>
            <v-list-item-content>
              <div @click="mylist">
                <span>내가 쓴 글 보기</span>
              </div>
            </v-list-item-content>
          </v-list-item>

          <v-list-item link>
            <v-list-item-action>
              <v-icon>mdi-cog</v-icon>
            </v-list-item-action>
            <v-list-item-content>
              <div @click="logout">
                <span>로그아웃</span>
              </div>
            </v-list-item-content>
          </v-list-item>
        </v-list>
      </v-navigation-drawer>
    </div>

    <!-- 네비게이션 -->
    <v-app-bar app color="#B677FF" clipped-right dark>
      <v-toolbar-title @click="hompage">싸피탈출넘버원</v-toolbar-title>
      <v-spacer></v-spacer>
      <div v-if="this.$session.get('uid') == null">
        <button @click="show">로그인</button>
        <!-- <router-link  @click="show"  v-bind:to="{name:constants.URL_TYPE.USER.LOGIN}" class="login-btn">
                로그인 
        </router-link>-->
      </div>
      <div v-else>
        <v-app-bar-nav-icon @click="drawer = !drawer"></v-app-bar-nav-icon>
      </div>
    </v-app-bar>
</div>
</template>   
<script>
import constants from "../../lib/constants";
import axios from "@/util/axios";

export default {
  props: {
    source: String,
  },
  data: () => ({
    drawer: null,
    constants,
    email: "",
    password: "",
    loginData: {
      email: "",
      password: "",
    },
    keyword: "",
  }),

  name: "MyComponent",
  methods: {
    show() {
      this.$modal.show("my-first-modal");
    },
    hide() {
      this.$modal.hide("my-first-modal");
    },
    signin: function () {
      axios({
        method: "POST",
        url: "/login",
        data: {
          password: this.loginData.password,
          email: this.loginData.email,
        },
      })
        .then((Response) => {
          this.$session.set("uid", Response.data);
          this.$modal.hide("my-first-modal");
          this.$router.go();
        })
        .catch((ex) => {
          var errorcode = ex.response.status;
          if (errorcode == 400) {
            alert("로그인에 실패하였습니다.");
          } else {
            this.$router.push("/errorpage/" + errorcode);
          }
        });
    },
    mypage: function () {
      axios({
        method: "GET",
        url: "/user/update",
        data: {
          email: this.email,
        },
      })
        .then((Response) => {})
        .catch((ex) => {
          var errorcode = ex.response.status;
          this.$router.push("/errorpage/" + errorcode);
        });
    },
    userupdate: function () {
      this.$router.push("/user/update");
    },
    mylist: function () {
      this.$router.push("/blog/list");
    },
    createblog: function () {
      console.log('gd')
      this.$router.push("/blog/blogcreate");
    },
    logout: function () {
      this.$session.destroy();
      this.$router.go();
    },
    search: function () {
      this.$router.push({
        path: "/blog/search",
        query: { keyword: this.keyword },
      });
    },
    hompage:function(){
      this.$router.push("/");
    }
  },
  mount() {
    this.show();
  },
};
</script>

<style>
.search_main{
  background-color:rgb(236, 190, 248);
}
span{
  margin: 15px;
}
</style>
