<template>
<v-app id="inspire"  >
  <div class="container" >
    <b-card style="background-color: #E3C4FF;">
      <div class="content-detail-content-info">
        <div class="content-detail-content-info-left">
          <div class="content-detail-content-info-left-subject">{{blog.title}}</div>
        </div>
        <div class="content-detail-content-info-right">
          <div class="content-detail-content-info-right-user">글쓴이: {{blog.uid}}</div>
        </div>
      </div>
      <div class="content-detail-content">{{blog.contents}}</div>
      <div class="content-detail-button text-center">
        <p>추천수: {{blog.likes}}  조회수: {{blog.views}}</p>
        <!-- 로그인한 사용자만 보이도록 추가해야함 -->
        <!-- 게시글 날짜도 콘솔 찍어보고 없으면 빽에 요청하자 -->
        <div class="">
          <b-button class="block-center" variant="primary">수정</b-button>&nbsp;
          <b-button variant="success">삭제</b-button>
        </div>
      </div>
    </b-card>
  </div>
   <v-footer app
    color="#B677FF"
    height="60px">
      <span class="footer_s">&copy; {{ new Date().getFullYear() }} 이 블로그의 저작권은 싸피탈출넘버원에있습니다. </span>
    </v-footer>
</v-app>
  <!-- </div> -->
</template>

<script>
import axios from "@/util/axios";

export default {
  name: "detail",
  data() {
    return {
      blog: {},
    };
  },
  mounted() {
    axios
      .get("/blog/detail/" + this.$route.params.bno)
      .then((response) => {
        this.blog = response.data;
      })
      .catch((error) => {
        var errorcode = ex.response.status;
        this.$router.push("/errorpage/" + errorcode);
      });
  },
};
</script>

<style scoped>
.content-detail-content-info {
  border: 1px solid black;
  display: flex;
  justify-content: space-between;
}

.content-detail-content-info-left {
  width: 720px;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  padding: 1rem;
}

.content-detail-content-info-right {
  width: 300px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 1rem;
}

.content-detail-content {
  border: 1px solid black;
  margin-top: 1rem;
  padding-top: 1rem;
  min-height: 720px;
}

.content-detail-button {
  border: 1px solid black;
  margin-top: 1rem;
  padding: 2rem;
}

</style>