<template>
<div>
  <div style="margin-top:5%">
    <h2>{{keyword}}</h2>
    
    <div class="container" >
    <table class="table">
      <thead class="thead-dark">
        <tr>
          <th scope="col">#</th>
          <th scope="col">제목</th>
          <th scope="col">닉네임</th>
          <th scope="col">조회수</th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="(blog, idx) in blogList" :key="idx">
          <th scope="row">{{blog.bno}}</th>

          <router-link :to="'/blog/detail/'+blog.bno">
            <td>{{blog.title}}</td>
          </router-link>

          <td>{{blog.uid}}</td>
          <td>{{blog.views}}</td>
        </tr>
      </tbody>
    </table>
    </div>

  <v-footer app
    color="#B677FF"
    height="60px">
      <span class="footer_s">&copy; {{ new Date().getFullYear() }} 이 블로그의 저작권은 싸피탈출넘버원에있습니다. </span>
    </v-footer>
</div>
</template>

<script>
import axios from "@/util/axios";

export default {
  name: "searchList",
  data() {
    return {
      keyword: "",
      blogList: {},
    };
  },
  mounted() {
    this.keyword = this.$route.query.keyword;

    axios
      .get("blog/search?keyword=" + this.keyword)
      .then((response) => {
        console.dir(response);
        this.blogList = response.data;
      })
      .catch((error) => {
        var errorcode = ex.response.status;
        this.$router.push("/errorpage/" + errorcode);
      });
  },
  methods: {},
};
</script>

