<template>
<div>

  <div class="container" >
    <div class="form-group">
        <label for="exampleFormControlInput1">제목</label>
        <input v-model="blog.title" type="email" class="form-control" id="exampleFormControlInput1">
    </div>
    <div class="form-group">
        <label for="exampleFormControlTextarea1">내용</label>
        <textarea v-model="blog.contents" class="form-control" id="exampleFormControlTextarea1" rows="4"></textarea>
    </div>
        <div class="form-group">
        <label for="exampleFormControlInput1">키워드</label>
        <input v-model="blog.keyword" type="email" class="form-control" id="exampleFormControlInput1">
    </div>
    <button @click="createblog" type="button" class="btn btn-primary">작성완료</button>
  </div>
  <v-footer app
    color="#B677FF"
    height="60px">
      <span class="footer_s">&copy; {{ new Date().getFullYear() }} 이 블로그의 저작권은 싸피탈출넘버원에있습니다. </span>
    </v-footer>

</div>
</template>

<script>
import axios from "@/util/axios";

export default {
    name: 'BlogCreate',
    
    data: () => {
        return {
             blog: {
                contents: "",
                keyword: "",
                title: "",
                uid: "",
            },
        }
    },
    mounted() {

    },
    methods: {
        createblog() {
            this.blog.uid=this.$session.get('uid')
            console.log(this.blog)
            
            axios({
                    method: "POST",
                    url: "/blog",
                    data: this.blog,
            })
                .then((Response) => {
                console.log("넣어저따")
                alert("포스팅이 완료되었습니다.");
                this.$router.push('/')
                })
                .catch((ex) => {
                console.log("에러!!~~");
            });
        }

    }

}

</script>

