import Vue from 'vue'
import Router from 'vue-router'

import constants from '../lib/constants'

// 유저
import Join from '../page/user/Join.vue'
import Update from '../page/user/Update.vue'

//blog
import MyList from '../page/blog/MyList.vue'
import searchList from "../page/blog/searchList"
import detail from "../page/blog/detail"
import BlogCreate from "../page/blog/blogCreate"

//게시판
import BoardList from "../page/board/BoardList"
import BoardDetail from "../page/board/BoardDetail"
import BoardCreate from "../page/board/BoardCreate"

// 포스트
import Frontpage from '../page/post/Frontpage.vue'

// ERROR
import ErrorPage from '../page/error/error.vue'

Vue.use(Router) 

export default new Router({
  mode:'history',
  routes: [   
    // 로그인/가입
    
    {
      path: '/user/join',
      name: constants.URL_TYPE.USER.JOIN,
      component: Join
    }, {
      path: '/user/update',
      name: 'update',
      component: Update
    },
    {
      path: '/blog/list',
      name: 'mylist',
      component: MyList
    },
    // 포스트
    { 
      path: '/',
      name: constants.URL_TYPE.POST.MAIN,
      component: Frontpage,
    },

    // 블로그
    {
      path: '/blog/search',
      name: constants.URL_TYPE.BLOG.SEARCHLIST,
      component: searchList
    },
    {
      path: '/blog/detail/:bno',
      name: constants.URL_TYPE.BLOG.DETAIL,
      component: detail
    },
    {
      path: "/blog/blogcreate",
      name: "BlogCreate",
      component: BlogCreate
    },
    
    //게시판
    {
      path: "/:keyword",
      name: "BoardList",
      component: BoardList
    },
    {
      path: "/board/detail/:contentId",
      name: "BoardDetail",
      component: BoardDetail
    },
    {
      path: "/board/create/:contentId?",
      name: "BoardCreate",
      component: BoardCreate
    },

    //error
    {
      path: "/errorpage/:code",
      name: "ErrorPage",
      component: ErrorPage
    },
    
  ]
})
